.header {
  margin-top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  max-width: 1024px;
}

.logo {
  display: flex;
  flex-direction: column;
  text-align: left;
  font-size: 5rem;
  margin: 0 4rem;
  color: #0A3871;
}

.btn-darkMode {
  margin-right: 4rem;
  width: 3rem;
}

.footer {
  margin-top: 10rem;
  text-align: center;
}
@media (min-width: 480px) {
  .footer {
    margin-top: 13rem;
  }
}

.input-palabra {
  display: flex;
  flex-direction: column;
  background-color: transparent;
  color: #04152b;
  border: none;
  font-size: 2.5rem;
  margin: 2rem auto;
  text-align: center;
  max-width: 768px;
}

.input-palabra:enabled {
  border: 0;
}

.muestra-palabra {
  display: flex;
  flex-direction: column;
  background-color: transparent;
  color: #04152b;
  border: none;
  font-size: 2.5rem;
  margin: 2rem auto;
  width: 100%;
}

.entrada {
  border: 2rem;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: rgba(10, 56, 113, 0.7);
}

.ingresar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.dark-mode {
  background-color: #333333;
}
.dark-mode .btn-darkMode {
  filter: invert(1);
}
.dark-mode .logo {
  color: #1266cf;
}
.dark-mode footer p {
  color: #FFFFFF;
}
.dark-mode span {
  color: #c8c8c8;
}
.dark-mode .input-palabra {
  color: #e1e1e1;
}
.dark-mode .muestra-palabra {
  color: #e1e1e1;
}

.botones {
  font-family: "Inter", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  width: 90%;
  margin: 15rem auto 0 auto;
}
@media (min-width: 480px) {
  .botones {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    width: 90%;
    column-gap: 2rem;
    max-width: 900px;
    height: 100%;
    margin: 25rem auto 0 auto;
  }
}

.botones-palabra {
  font-family: "Inter", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  width: 90%;
  margin: 12rem auto 0 auto;
}
@media (min-width: 480px) {
  .botones-palabra {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 90%;
    column-gap: 2rem;
    max-width: 900px;
    height: 100%;
    align-items: center;
    margin: 25rem auto 0 auto;
  }
  .botones-palabra span {
    grid-area: 1/1/span 1/span 2;
  }
}

.boton-nuevo {
  margin: 1rem;
  padding: 2rem;
  background-color: #0A3871;
  color: #FFFFFF;
  border: none;
  border-radius: 1.5rem;
  width: 100%;
  transition: all;
}
.boton-nuevo:hover {
  transition-duration: 400ms;
  transform: scale(0.95, 0.95);
}

.boton-palabra {
  margin: 1rem;
  padding: 2rem;
  background-color: #e1e1e1;
  border-radius: 1.5rem;
  border: 2px solid #0A3871;
  width: 100%;
  transition: all;
  color: #0A3871;
}
.boton-palabra:hover {
  transition-duration: 400ms;
  transform: scale(0.95, 0.95);
}

.boton-guardar {
  margin: 1rem;
  padding: 2rem;
  background-color: #0A3871;
  color: #FFFFFF;
  border: none;
  border-radius: 1.5rem;
  width: 100%;
  transition: all;
}
.boton-guardar:hover {
  transition-duration: 400ms;
  transform: scale(0.95, 0.95);
}

.boton-cancelar {
  margin: 1rem;
  padding: 2rem;
  background-color: #e1e1e1;
  border-radius: 1.5rem;
  border: 2px solid #0A3871;
  width: 100%;
  transition: all;
  color: #0A3871;
}
.boton-cancelar:hover {
  transition-duration: 400ms;
  transform: scale(0.95, 0.95);
}

span {
  display: flex;
  flex-direction: row;
  font-size: 1.5rem;
}

span img {
  margin: auto 1rem;
  width: 2.5rem;
}

.ft-palabraNueva {
  margin: 5rem auto 0 auto;
  padding: 0;
  text-align: center;
}

.botones-adivinar {
  font-family: "Inter", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  width: 90%;
  margin: 5rem auto 0 auto;
}
@media (min-width: 480px) {
  .botones-adivinar {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 90%;
    column-gap: 2rem;
    max-width: 900px;
    height: 100%;
    align-items: center;
  }
  .botones-adivinar span {
    grid-area: 1/1/span 1/span 2;
  }
}

.guillotina {
  width: 70%;
  margin: 4rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.guillotina img {
  width: 20rem;
}

.errado {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin: 2rem auto;
}

.adivina {
  color: #0070C0;
  font-size: 7rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin: 0 auto;
}

.letras {
  color: #0070C0;
  font-size: 7rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.letra {
  display: inline-block;
  text-align: center;
}

.adivinada {
  color: #0070C0;
  font-size: 4rem;
  width: 5.5rem;
  height: 5.5rem;
  border-bottom: solid 3px #0A3871;
  margin-left: 0.4rem;
  line-height: 4.5rem;
}

.errada {
  margin-left: 1rem;
}

.malas .gana {
  color: #E08709;
  font-size: 3rem;
  text-align: center;
}
.malas .mala {
  color: #fc1010;
  font-size: 3rem;
  text-align: center;
}
.malas .ocultar {
  display: none;
}